/*=================fonts=================*/
* {
  margin: 0;
  padding: 0; }

._container {
  max-width: 1180px;
  padding: 0 20px;
  margin: auto; }

.header {
  background: url("../img/header-nav.jpg") top no-repeat; }
  .header .top-header__content {
    display: flex;
    align-items: center;
    height: 90px; }
  .header .top-header__column {
    flex: 0 1 50%; }
    .header .top-header__column_logo {
      align-self: flex-start;
      padding-top: 25px;
      flex: 0 0 190px;
      text-align: center; }
    .header .top-header__column .menu__list {
      display: grid;
      grid-template-columns: 110px 130px 180px; }
      .header .top-header__column .menu__list li {
        list-style: none;
        margin: 1px 0; }
    .header .top-header__column .menu__link {
      font-family: 'Play-Bold';
      white-space: nowrap;
      font-size: 14px;
      min-height: 27px;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      color: #ffffff;
      padding: 2px 22px; }
    .header .top-header__column .icon-menu {
      display: none; }
    .header .top-header__column .contacts-header {
      display: flex;
      justify-content: center; }
      .header .top-header__column .contacts-header__column {
        padding: 0 30px;
        border-right: 1px solid #ffffff; }
        .header .top-header__column .contacts-header__column:last-child {
          border: none; }
      .header .top-header__column .contacts-header__item_icon {
        background: url("../img/icons/phone.png") left no-repeat;
        padding: 0px 0px 0px 37px; }
      .header .top-header__column .contacts-header__label {
        font-family: 'Roboto-Light';
        color: #ffffff;
        font-size: 12px;
        margin-bottom: 5px; }
      .header .top-header__column .contacts-header__phone {
        font-family: 'Roboto-Black';
        color: #ffffff;
        font-size: 18px;
        text-decoration: none;
        white-space: nowrap; }
  .header .bottom-header__content {
    display: flex;
    height: 63px;
    align-items: center;
    justify-content: space-between; }
  .header .bottom-header__column {
    flex: 0 1 calc(50% - 95px); }
  .header .bottom-header .actions-header {
    display: flex;
    align-items: center; }
    .header .bottom-header .actions-header li {
      font-family: 'Roboto-Light';
      list-style: none;
      margin: 0px 54px 0px 0px; }
    .header .bottom-header .actions-header__item {
      padding-left: 36px;
      display: inline-block;
      line-height: 24px;
      text-decoration: none;
      color: #999999; }
      .header .bottom-header .actions-header__item span {
        font-size: 14px;
        border-bottom: 2px dotted #999999; }
        .header .bottom-header .actions-header__item span:hover {
          color: #1ab9ce;
          border-bottom: 2px dotted #1ab9ce; }
      .header .bottom-header .actions-header__item_login {
        background: url("../img/icons/login.png") left no-repeat; }
      .header .bottom-header .actions-header__item_reg {
        background: url("../img/icons/registration.png") left no-repeat; }
      .header .bottom-header .actions-header__item_email {
        background: url("../img/icons/mail.png") left no-repeat; }
        .header .bottom-header .actions-header__item_email span {
          border-bottom: 1px solid; }
          .header .bottom-header .actions-header__item_email span:hover {
            border-bottom: 1px solid; }
  .header .bottom-header .info-header {
    display: flex;
    justify-content: center;
    align-items: center; }
    .header .bottom-header .info-header__column {
      font-family: 'Roboto-Light';
      padding: 0px 35px 0px 0px; }
    .header .bottom-header .info-header span {
      font-size: 14px; }
    .header .bottom-header .info-header__callback {
      padding-left: 36px;
      background: url("../img/icons/callback.png") left no-repeat;
      display: inline-block;
      color: #1ab9ce;
      text-decoration: none;
      white-space: nowrap; }
      .header .bottom-header .info-header__callback span {
        border-bottom: 2px dotted #1ab9ce; }
    .header .bottom-header .info-header__schedule {
      padding-left: 36px;
      color: #999999;
      background: url("../img/icons/schedule.png") left no-repeat;
      line-height: 18px; }
      .header .bottom-header .info-header__schedule span {
        font-family: 'Roboto-Black'; }
      .header .bottom-header .info-header__schedule p {
        font-size: 14px; }
    .header .bottom-header .info-header__cart {
      width: 43px;
      height: 43px;
      display: inline-block;
      background: url("../img/icons/cart-blue.png") 0 0 no-repeat;
      position: relative; }
      .header .bottom-header .info-header__cart span {
        padding: 0 11px;
        border-radius: 20px;
        line-height: 25px;
        background: #f68038;
        position: absolute;
        right: 0;
        transform: translate(60%, 0px);
        color: #ffffff;
        font-size: 18px;
        font-family: 'Roboto-Bold'; }
